<script>
	export default {
		data() {
			return {
				getPoint:{
					change_score:"0",//今日签到获得的积分
					tomorrow_score:"0",//明日签到能获得的积分
				},//用来接收签到接口返回参数
				showSingInSuccess:false,//签到成功弹出层
				showPop:false,//规则说明弹出层				
				singInNum:{
					set_info:[]
				},//积分
				taskList:[
					{
						url:"/static/taskImg1.png",
						taskTile:"关注“爱加优购”公众号",
						taskContent:"成功关注后可获得",
						taskContent2:'',
						taskPoint:"200积分",
						taskBtn:"去关注"
					},
					{
						url:"/static/taskImg2.png",
						taskTile:"邀请好友",
						taskContent:"1.推荐成功一位且好友关注公众号可获得",
						taskContent2:'2.下级会员消费，可获得其10%积分奖励',
						taskPoint:"50积分",
						taskBtn:"去邀请"
					},
					{
						url:"/static/taskImg3.png",
						taskTile:"购买商品",
						taskContent:"花费多少就获得多少积分",
						taskContent2:'',
						taskPoint:"",
						taskBtn:"去首页"
					},
				],//任务列表内容,现在是写死的.如果以后要写活直接调接口让后台返回相同的数据结构就行,请求里接收依稀
			}
		},
		onShow(){
			let self=this
			self.request({
				url:"ugo/api/app.php?c=account/getSignInfo",
				data:{}
			}).then(res => {
			        if (res.data.success) {
						this.singInNum=res.data.data
			        } else {
								uni.showToast({
								title: res.data.message,
								icon: 'none'
			                })
			        }
			})
		},
		methods: {
			// 签到
			singInSuccess(e){
				let self=this;
				if(e==self.singInNum.total_day){
					self.request({
						url:'ugo/api/app.php?c=account/clickSign',
						data:{}
					}).then(res => {
							console.log(res)
					        if (res.data.success) {
								self.getPoint=res.data.data
								self.showSingInSuccess=true
					        } else {
					                uni.showToast({
					                title: res.data.message,
					                icon: 'none'
					                })
					        }
					})
				}else{
					
				}
			},
			// 返回对应的签到周期天数
			returnDayNum(e){	
				let day=['一','二','三','四','五','六','七']
				return day[e]
			},
			// 积分规则弹窗确定按钮
			popOk(){
				this.showPop=false
			},
			//打开积分规则说明模态框
			showRulePop(){
				this.showPop=true
			},
			// 跳转到积分兑换记录页面
			goExchangeRecord(){
				uni.navigateTo({
					url:"exchangesRecord"
				})
			},
			// 跳转到积分商城
			goPointShop(){
				uni.navigateTo({
					url:"exchangeshop"
				})
			},
			// 跳转到积分明细页面
			goPointDetails(){
				uni.navigateTo({
					url:"pointsDetails"
				})
			},
			// 跳转到转赠页面
			goPointTransfer(){
				uni.navigateTo({
					url:"IntegralTransferred"
				})
			},
			// 跳转到订单记录页面
			goMyRecord(){
				uni.navigateTo({
					url:"myRecord"
				})
			}
		}
	}
</script>

